import React from 'react'
import ListSongs from './ListSongs'

function List( {songs} ) {
    return (
        <div className="list">
            <h2>Music List</h2>
            <div className="list-songs">
                {songs.map(song =>
                    <ListSongs song={song}/>
                )}
            </div>
        </div>
    )
}

export default List
